@import '../../styles/variables';
@import '../../styles/mixins';

.filter {
  // .filter__body
  &__body {
    width: 200px;
    padding: 25px 0 0;
    border-radius: 5px;
    box-shadow: 0 0 10px lightgrey;
  }

  // .filter__title
  &__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    //border-bottom: 1px solid lightgrey;
    box-shadow: 0 6px 10px -2px lightgrey;
    padding: 0 25px 20px;
    border-radius: 5px;
  }

  // .filter__arrow-icon
  &__arrow-icon {
    img {
      transition: all .4s;
    }
  }

  // .filter__menu
  &__menu {
  }
}

.menu-filter {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  transform-origin: top;
  transition: height, padding .4s,
    transform .2s;

}