@import "../../styles/variables";
@import "../../styles/mixins";

.banner {
  margin-top: 30px;
  // .banner__body
  &__body {
    border-radius: 5px;
    padding: 25px;
    box-shadow: 0 0 10px lightgrey;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include adaptive-value('gap', 40, 20, 1);
  }

  // .banner__title
  &__title {
  }

  // .banner__img
  &__img {
    max-width: 150px;
    max-height: 150px;
    & img {
      width: 140px;
      height: 135px;
    }
  }

  // .banner__button
  &__button {
    background-color: $buttonColor1;
    border: 1px solid $buttonColor1;
    border-radius: 20px;
    min-width: 100px;
    min-height: 40px;
    transition: all .2s;
    & span {
      color: white;
      transition: all .2s;
    }
  }
}

.banner__button:hover {
  min-width: 95px;
  & span {
    font-size: 12px;
  }
}