.header {
  height: 90px;
  color: #4D5E80;
  display: flex;
  align-items: center;
  box-shadow: 0 0 10px #C3CAD9;
}
@media (max-width: 1440px) {
  .header {
    height: calc(50px + 40 * ((100vw - 320px) / 1120));
  }
}
.header__container {
  width: 100%;
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.logo-header {
  display: flex;
  gap: 30px;
  align-items: center;
}
@media (max-width: 1440px) {
  .logo-header {
    gap: calc(10px + 20 * ((100vw - 320px) / 1120));
  }
}
.logo-header__burger-menu {
  width: 20px;
  height: 30px;
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.logo-header__burger-menu span {
  position: absolute;
  display: block;
  height: 2px;
  width: 100%;
  background-color: #C3CAD9;
  border-radius: 10px;
}
.logo-header__burger-menu span:before, .logo-header__burger-menu span:after {
  content: "";
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background-color: #C3CAD9;
  border-radius: 10px;
}
.logo-header__burger-menu span:before {
  top: -6px;
}
.logo-header__burger-menu span:after {
  top: 6px;
}
.logo-header__title {
  cursor: default;
}

.menu-header__body {
  display: flex;
  gap: 30px;
}
@media (max-width: 1440px) {
  .menu-header__body {
    gap: calc(10px + 20 * ((100vw - 320px) / 1120));
  }
}

.search-header {
  max-width: 140px;
}
.search-header__body {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 1440px) {
  .search-header__body {
    gap: calc(10px + 10 * ((100vw - 320px) / 1120));
  }
}
.search-header__img {
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.search-header__img img {
  width: 30px;
  height: 30px;
}
.search-header__placeholder input {
  background-color: #EDEFF2;
  font-family: "Roboto";
  font-size: 16px;
}
.search-header__placeholder input::placeholder {
  color: #C3CAD9;
}

