.account {
  display: flex;
  gap: 30px;
}
@media (max-width: 1440px) {
  .account {
    gap: calc(15px + 15 * ((100vw - 320px) / 1120));
  }
}
.account__body {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 1440px) {
  .account__body {
    gap: calc(10px + 10 * ((100vw - 320px) / 1120));
  }
}
.account__img {
  width: 30px;
  height: 30px;
}
.account__img img {
  width: 30px;
  height: 30px;
}

