.menu-filter {
  gap: 30px;
}
@media (max-width: 1440px) {
  .menu-filter {
    gap: calc(25px + 5 * ((100vw - 320px) / 1120));
  }
}
.menu-filter__list-item {
  width: 70px;
  display: flex;
  align-items: center;
  gap: 10px;
}
@media (max-width: 1440px) {
  .menu-filter__list-item {
    gap: calc(8px + 2 * ((100vw - 320px) / 1120));
  }
}
.menu-filter__list-item div {
  width: 20px;
  height: 20px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}
.menu-filter__list-item div img {
  transition: opacity 0.2s;
}
.menu-filter__list-item span {
  width: 40px;
}

