@import '../../styles/variables';
@import '../../styles/mixins';

.account {
  display: flex;
  @include adaptive-value('gap', 30, 15, 1);
  // .account__body
  &__body {
    display: flex;
    align-items: center;
    @include adaptive-value('gap', 20, 10, 1);
  }

  // .account__img
  &__img {
    width: 30px;
    height: 30px;
    & img {
      width: 30px;
      height: 30px;
    }
  }

  // .account__title
  &__title {
  }
}