.banner {
  margin-top: 30px;
}
.banner__body {
  border-radius: 5px;
  padding: 25px;
  box-shadow: 0 0 10px lightgrey;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}
@media (max-width: 1440px) {
  .banner__body {
    gap: calc(20px + 20 * ((100vw - 320px) / 1120));
  }
}
.banner__img {
  max-width: 150px;
  max-height: 150px;
}
.banner__img img {
  width: 140px;
  height: 135px;
}
.banner__button {
  background-color: #FF6633;
  border: 1px solid #FF6633;
  border-radius: 20px;
  min-width: 100px;
  min-height: 40px;
  transition: all 0.2s;
}
.banner__button span {
  color: white;
  transition: all 0.2s;
}

.banner__button:hover {
  min-width: 95px;
}
.banner__button:hover span {
  font-size: 12px;
}

