@import "nullstyle";
@import "fonts";
//@import "icons";
@import "variables";
@import "mixins";


body {
  background-color: $mainColor;
  color: $fontColor;
  font-family: $fontFamily;
  font-size: $fontSize+px;
  height: 100%;
}

[class*="_container"] {
  max-width: $maxWidthContainer+px;
  padding: 0 15px;
  margin: 0 auto;
}

._weight700 {
  font-weight: $fontWeight;
}

._pointer {
  cursor: pointer;
}

