.card {
  max-height: 400px;
}
.card__body {
  width: 250px;
  height: 350px;
  border-radius: 5px;
  padding: 25px 25px 40px;
  box-shadow: 0 0 10px lightgrey;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
}
@media (max-width: 1440px) {
  .card__body {
    gap: calc(40px + 20 * ((100vw - 320px) / 1120));
  }
}
.card__img {
  max-width: 180px;
  max-height: 118px;
}
.card__information {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
@media (max-width: 1440px) {
  .card__information {
    gap: calc(15px + 5 * ((100vw - 320px) / 1120));
  }
}

