@import '../../styles/variables';
@import '../../styles/mixins';

.menu-filter {
  @include adaptive-value('gap', 30, 25, 1);
  // .menu-filter__list-item
  &__list-item {
    width: 70px;
    display: flex;
    align-items: center;
    @include adaptive-value('gap', 10, 8, 1);
    & div {
      width: 20px;
      height: 20px;
      border-radius: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all .2s;
      & img {
        transition: opacity .2s;
      }
    }
    & span {
      width: 40px;
    }
  }
}