@import "../../styles/variables";
@import "../../styles/mixins";

.header {
  @include adaptive-value('height', 90, 50, 1);
  color: $fontColor;
  display: flex;
  align-items: center;
  box-shadow: 0 0 10px $grayColor;
  // .header__container
  &__container {
    width: 100%;
  }

  // .header__body
  &__body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  // .header__logo
  &__logo {
  }

  // .header__menu
  &__menu {
  }

  // .header__search
  &__search {
  }

  // .header__account
  &__account {
  }
}

.logo-header {
  display: flex;
  @include adaptive-value('gap', 30, 10, 1);
  align-items: center;
  // .logo-header__burger-menu
  &__burger-menu {
    width: 20px;
    height: 30px;
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    & span {
      position: absolute;
      display: block;
      height: 2px;
      width: 100%;
      background-color: $grayColor;
      border-radius: 10px;
      &:before, &:after {
        content: "";
        display: block;
        position: absolute;
        height: 2px;
        width: 100%;
        background-color: $grayColor;
        border-radius: 10px;
      }
      &:before {
        top: -6px;
      }
      &:after {
        top: 6px;
      }
    }
  }

  // .logo-header__title
  &__title {
    cursor: default;
  }
}

.menu-header {
  // .menu-header__body
  &__body {
    display: flex;
    @include adaptive-value('gap', 30, 10, 1);
  }
}

.search-header {
  // .search-header__body
  max-width: 140px;
  &__body {
    display: flex;
    align-items: center;
    @include adaptive-value('gap', 20, 10, 1);
  }

  // .search-header__img
  &__img {
    width: 30px;
    height: 30px;
    cursor: pointer;
    & img {
      width: 30px;
      height: 30px;
    }
  }

  // .search-header__placeholder
  &__placeholder {
    & input {
      background-color: $mainColor;
      font-family: $fontFamily;
      font-size: $fontSize+px;
      &::placeholder{
        color: $grayColor;
      }
    }
  }
}

