.filter__body {
  width: 200px;
  padding: 25px 0 0;
  border-radius: 5px;
  box-shadow: 0 0 10px lightgrey;
}
.filter__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 6px 10px -2px lightgrey;
  padding: 0 25px 20px;
  border-radius: 5px;
}
.filter__arrow-icon img {
  transition: all 0.4s;
}
.menu-filter {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  transform-origin: top;
  transition: height, padding 0.4s, transform 0.2s;
}

