@import "../../styles/variables";
@import "../../styles/mixins";

.card {
  max-height: 400px;
  // .card__body
  &__body {
    width: 250px;
    height: 350px;
    border-radius: 5px;
    padding: 25px 25px 40px;
    box-shadow: 0 0 10px lightgrey;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include adaptive-value('gap', 60, 40, 1)
  }

  // .card__img
  &__img {
    max-width: 180px;
    max-height: 118px;
    & img {

    }
  }

  // .card__information
  &__information {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include adaptive-value('gap', 20, 15, 1);
  }

  // .card__category
  &__category {
  }

  // .card__title
  &__title {
  }

  // .card__price
  &__price {
  }
}