.sidebar-element {
  width: 200px;
}
@media (max-width: 1440px) {
  .sidebar-element {
    width: calc(150px + 50 * ((100vw - 320px) / 1120));
  }
}
.sidebar-element__body {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 1440px) {
  .sidebar-element__body {
    gap: calc(10px + 10 * ((100vw - 320px) / 1120));
  }
}
.sidebar-element__img {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

